 membawa perubahan dalam penyampaian materi ajar, dimana karyawan dapat belajar dimana saja secara online, sehingga proses belajar mengajar dapat menjadi lebih cepat dan terencana. Divisi Riset dan Pendidikan juga turut berkembang seiring dengan kemajuan teknologi dengan terus menerus mengembangkan materi e-learning. Kami harapkan hal ini membuat dokter, perawat, staff dan calon staff JEC Eye Hospitals and Clinics lebih siap dalam menghadapi proses yang terjadi di lapangan. The purpose of learning is growth, and it holds the passport to the future. Selamat belajar!")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"grid grid-cols-1 gap-8 lg:grid-cols-2"},[_c('div',[_c('img',{staticClass:"h-40 w-auto object-cover object-center mx-auto mb-4",attrs:{"src":require("@/assets/photos/prof-tdg.png"),"alt":"Prof. Dr. Tjahjono D. Gondhowiardjo, SpM(K), PhD"}}),_c('div',{staticClass:"text-center"},[_c('h3',{staticClass:"text-blue-800 text-xl font-bold"},[_vm._v("Prof. Dr. Tjahjono D. Gondhowiardjo, SpM(K), PhD")]),_c('span',{staticClass:"text-green-500"},[_vm._v("Director of Education and Development")])])]),_c('div',[_c('img',{staticClass:"h-40 w-auto object-cover object-center mx-auto mb-4",attrs:{"src":require("@/assets/photos/dr-rini.png"),"alt":"Dr. Rini Sulastiwaty, SpM"}}),_c('div',{staticClass:"text-center"},[_c('h3',{staticClass:"text-blue-800 text-xl font-bold"},[_vm._v("Dr. Rini Sulastiwaty, SpM")]),_c('span',{staticClass:"text-green-500"},[_vm._v("Head of Research and Education")])])])])}]

